import React from 'react';
import { Route, IndexRoute } from 'react-router';
import TerminalMakerView from './containers/setup/terminalmaker/create';
import ReadTerminalView from './containers/setup/terminalmaker/read';
import TerminalMakerEditView from './containers/setup/terminalmaker/edit';
import SimTypeCreateView from './containers/setup/sim_type/create'

import NotFoundView from './containers/NotFound';

export default(
    <Route path="/">
        <IndexRoute component={TerminalMakerView} />
        <Route path="terminal-maker/create" component={TerminalMakerView} />
        <Route path="terminal-maker/view" component={ReadTerminalView} />
        <Route path="terminal-maker/edit/:maker_name" component={TerminalMakerEditView} />

        <Route path="sim-type/create" component={SimTypeCreateView} />
        

        <Route path="*" component={NotFoundView} />
    </Route>
);


