import axios from 'axios';
import { SERVER_URL } from '../../../utils/config';
import { checkHttpStatus } from '../../../utils';
import fetch from 'isomorphic-fetch';
import {
    TERMINAL_MAKER_CREATE_REQUEST,TERMINAL_MAKER_CREATE_SUCCESS, TERMINAL_MAKER_CREATE_FAILURE,
    TERMINAL_MAKER_FETCH_REQUEST, TERMINAL_MAKER_FETCH_SUCCESS, TERMINAL_MAKER_FETCH_FAILURE,
    TERMINAL_MAKER_EDIT_REQUEST, TERMINAL_MAKER_EDIT_SUCCESS, TERMINAL_MAKER_EDIT_FAILURE,
    TERMINAL_MAKER_DELETE_REQUEST, TERMINAL_MAKER_DELETE_SUCCESS, TERMINAL_MAKER_DELETE_FAILURE
} from '../../../constants/setup/terminalmaker';


export function createRequest() {
    return {
        type: TERMINAL_MAKER_CREATE_REQUEST
    };
}

export function createSuccess() {
    return {
        type: TERMINAL_MAKER_CREATE_SUCCESS
    };
}

export function createFailure(error, message) {
    return {
        type: TERMINAL_MAKER_CREATE_FAILURE,
        payload: {
            status: error,
            statusText: message
        }
    };
}

export function createAction(payload) {
    return (dispatch) => {
        dispatch(createRequest());
        return axios(`${SERVER_URL}/setup/terminal-maker/`, JSON.stringify({
                maker_code: payload.maker_code,
                maker_name: payload.maker_name,
                address: payload.address,
                contact_person: payload.contact_person,
                contact_number: payload.contact_number,
                country_of_origin: payload.country_of_origin
        }))
            .then(checkHttpStatus)
            .then((response) => {
                dispatch(createSuccess());
            })
            .catch((error) => {
                if (error && typeof error.response !== 'undefined' && error.response.status >= 500) {
                    // Server side error
                    dispatch(createFailure(500, 'A server error occurred while sending your data!'));
                } else {
                    // Most likely connection issues
                    dispatch(createFailure('Connection Error', 'An error occurred while sending your data!'));
                }
                return Promise.resolve();
            });
    };
}

export function fetchRequest() {
    return {
        type: TERMINAL_MAKER_FETCH_REQUEST
    };
}

export function fetchSuccess(data) {
    return {
        type: TERMINAL_MAKER_FETCH_SUCCESS,
        payload: {
            data
        }
    };
}

export function fetchFailure(error, message) {
    return {
        type: TERMINAL_MAKER_FETCH_FAILURE,
        payload: {
            status: error,
            statusText: message
        }
    };
}

export function readAction(payload) {
    return (dispatch) => {
        dispatch(fetchRequest());
        return axios.get(`${SERVER_URL}/setup/terminal-maker/`)
        //return fetch(`${SERVER_URL}/api/setup/terminal-maker`)
            .then(checkHttpStatus)
            .then((response) => {
                dispatch(fetchSuccess(response.data));
            })
            .catch((error) => {
                if (error && typeof error.response !== 'undefined' && error.response.status >= 500) {
                    // Server side error
                    dispatch(fetchFailure(500, 'A server error occurred while sending your data!'));
                } else {
                    // Most likely connection issues
                    dispatch(fetchFailure('Connection Error', 'An error occurred while sending your data!'));
                }
                return Promise.resolve();
            });
    };
}

export function editRequest() {
    return {
        type: TERMINAL_MAKER_EDIT_REQUEST
    };
}

export function editSuccess(data) {
    return {
        type: TERMINAL_MAKER_EDIT_SUCCESS,
        payload: {
            data
        }
    };
}

export function editFailure(error, message) {
    return {
        type: TERMINAL_MAKER_EDIT_FAILURE,
        payload: {
            status: error,
            statusText: message
        }
    };
}


export function updateAction(payload) {
    return (dispatch) => {
        dispatch(editRequest());
        return axios.put(`${SERVER_URL}/setup/terminalmaker/`, {
                maker_code: payload.maker_code,
                maker_name: payload.maker_name,
                address: payload.address,
                contact_person: payload.contact_person,
                contact_number: payload.contact_number,
                country_of_origin: payload.country_of_origin
        })
            .then(checkHttpStatus)
            .then((response) => {
                dispatch(editSuccess());
            })
            .catch((error) => {
                if (error && typeof error.response !== 'undefined' && error.response.status >= 500) {
                    // Server side error
                    dispatch(editFailure(500, 'A server error occurred while sending your data!'));
                } else {
                    // Most likely connection issues
                    dispatch(editFailure('Connection Error', 'An error occurred while sending your data!'));
                }
                return Promise.resolve();
            });
    };
}

export function deleteRequest() {
    return {
        type: TERMINAL_MAKER_DELETE_REQUEST
    };
}

export function deleteSuccess(data) {
    return {
        type: TERMINAL_MAKER_DELETE_SUCCESS,
        payload: {
            data
        }
    };
}

export function deleteFailure(error, message) {
    return {
        type: TERMINAL_MAKER_DELETE_FAILURE,
        payload: {
            status: error,
            statusText: message
        }
    };
}


export function deleteAction(payload) {
    return (dispatch) => {
        dispatch(deleteRequest());
        return axios.delete(`${SERVER_URL}/setup/terminal-maker/`, {
                terminal_id: payload.terminal_id
        })
            .then(checkHttpStatus)
            .then((response) => {
                dispatch(deleteSuccess());
            })
            .catch((error) => {
                if (error && typeof error.response !== 'undefined' && error.response.status >= 500) {
                    // Server side error
                    dispatch(deleteFailure(500, 'A server error occurred while sending your data!'));
                } else {
                    // Most likely connection issues
                    dispatch(deleteFailure('Connection Error', 'An error occurred while sending your data!'));
                }
                return Promise.resolve();
            });
    };
}


