import { combineReducers } from 'redux';
import { routerReducer } from 'react-router-redux';
import terminalMakerReducer from './setup/terminalmaker';
import simTypeReducer from './setup/sim_type';

export default combineReducers({
    terminalMaker: terminalMakerReducer,
    simType: simTypeReducer,
    routing: routerReducer
});


