import React, { Component } from 'react';
import t from 'tcomb-form';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import * as simTypeActionCreators from '../../../actions/setup/sim_type';

const Form = t.form.Form;

const SimTypeForm = t.struct({
    sim_code: t.String,
    sim_type: t.String,
    network: t.String,
    others: t.maybe(t.String)
});

const SimTypeFormOptions = {
    error: <i>Form not filled completely</i>,
    auto: 'placeholders',
    help: <i>Create Sim  Type</i>,
};


class SimTypeCreateView extends Component {

    constructor(props) {
        super(props);
        this.state = {
            formValues: {
               sim_code: '',
               sim_type: '',
               network: '',
               others: ''
            }
        }
    }

    _handleSubmit = (e) => {
        e.preventDefault();
        const value = this.refs.form.getValue();
        if (value) {
            const payload = {
                sim_code: this.state.formValues.sim_code,
                sim_type: this.state.formValues.sim_type,
                network: this.state.formValues.network,
                others: this.state.formValues.others
            }
            this.props.actions.createAction(payload);
        }
    };

    onFormChange = (value) => {
        this.setState({ formValues: value });
    };

    render() {
        if (this.props.statusText) {
            const statusTextClassNames = {
                'alert': true,
                'alert-danger': this.props.statusText.indexOf('A server error') === 0,
                'alert-success': this.props.statusText.indexOf('A server error') !== 0
            };

            let statusText = (
                <div className="row">
                    <div className="col-sm-12">
                        <div className={statusTextClassNames}>
                            {this.props.statusText}
                        </div>
                    </div>
                </div>
            );
        }
        return (
            <div className="col-sm-6 col-sm-offset-3">
                <form onSubmit={this._handleSubmit}>
                <Form ref='form'
                    type={SimTypeForm}
                    options={SimTypeFormOptions}
                    value={this.state.formValues}
                    onChange={this.onFormChange}
                />
                <button disabled={this.props.isFetching}
                    type="submit"
                    className="btn btn-primary btn-block"
                >
                    Submit
                </button>
            </form> 
        </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        isFetching: state.terminalMaker.isFetching,
        statusText: state.terminalMaker.statusText
    };
};

const mapDispatchToProps = (dispatch) => {
    return {
        dispatch,
        actions: bindActionCreators(simTypeActionCreators, dispatch)
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(SimTypeCreateView);


