import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { Link } from 'react-router'
import * as terminalMakerActionCreators from '../../../actions/setup/terminal_maker';


class ReadTerminalView extends Component {

    componentWillMount() {
        this.props.actions.readAction();
    }

    handleEdit = (id) => {
        console.log(id);
    }

    handleDelete = (id) => {
        let payload = {
            terminal_id: id
        }
        this.props.actions.deleteAction(payload);
    }

    render() {
        return (
            <div>
                {this.props.statusText !== 'Terminal Maker Read successfully.' ? <p>Loading data....</p> : 
                    <table className="table table-bordered table-hover">
                    <tbody>
                    <tr className="alert-info">
                        <th>Code</th>
                        <th>Maker Name</th>
                        <th>Address</th>
                        <th>Country of Origin</th>
                        <th>Contact Person</th>
                        <th>Telephone</th>
                        <th>Edit</th>
                        <th>Delete</th>
                    </tr>
                    
                    {this.props.data.map((terminal) => {
                        const url = `/terminal-maker/edit/${terminal.maker_name}`;
                        return (<tr key={terminal.id}>
                            <td>{terminal.terminal_code}</td>
                            <td>{terminal.maker_name}</td>
                            <td>{terminal.address}</td>
                            <td>{terminal.country_of_origin}</td>
                            <td>{terminal.contact_person}</td>
                            <td>{terminal.contact_person}</td>
                            <td>
                                <Link to={url}>
                                    <button onClick={this.handleEdit} className="btn btn-info">Edit</button>
                                </Link>
                            </td>
                                
                            <td><button onClick={() => this.handleDelete(terminal.terminal_id)} id={terminal.terminal_id} className="btn btn-danger">Delete</button></td>
                        </tr>);
                    })}
                    </tbody>
                </table>
                }
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        data: state.terminalMaker.data,
        isFetching: state.terminalMaker.isFetching,
        statusText: state.terminalMaker.statusText
    };
};
const mapDispatchToProps = (dispatch) => {
    return {
        actions: bindActionCreators(terminalMakerActionCreators, dispatch)
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(ReadTerminalView);


