import { createReducer } from '../../../utils';
import {
    TERMINAL_MAKER_CREATE_REQUEST,TERMINAL_MAKER_CREATE_SUCCESS, TERMINAL_MAKER_CREATE_FAILURE,
    TERMINAL_MAKER_FETCH_REQUEST, TERMINAL_MAKER_FETCH_SUCCESS, TERMINAL_MAKER_FETCH_FAILURE,
    TERMINAL_MAKER_EDIT_REQUEST, TERMINAL_MAKER_EDIT_SUCCESS, TERMINAL_MAKER_EDIT_FAILURE,
    TERMINAL_MAKER_DELETE_REQUEST, TERMINAL_MAKER_DELETE_SUCCESS, TERMINAL_MAKER_DELETE_FAILURE
} from '../../../constants/setup/terminalmaker';

const initialState = {
    isFetching: false,
    statusText: null,
    data: null
};

export default createReducer(initialState, {
    [TERMINAL_MAKER_CREATE_REQUEST]: (state, payload) => {
        return Object.assign({}, state, {
            isFetching: true,
            statusText: null
        });
    },
    [TERMINAL_MAKER_CREATE_SUCCESS]: (state, payload) => {
        return Object.assign({}, state, {
            isFetching: false,
            statusText: 'Terminal Maker created successfully.'
        });
    },
    [TERMINAL_MAKER_CREATE_FAILURE]: (state, payload) => {
        return Object.assign({}, state, {
            isFetching: false,
            statusText: `Error: ${payload.status} - ${payload.statusText}`
        });
    },
    [TERMINAL_MAKER_FETCH_REQUEST]: (state, payload) => {
        return Object.assign({}, state, {
            isFetching: true
        });
    },
    [TERMINAL_MAKER_FETCH_SUCCESS]: (state, payload) => {
        return Object.assign({}, state, {
            isFetching: false,
            statusText: 'Terminal Maker Read successfully.',
            data: payload.data
        });
    },
    [TERMINAL_MAKER_FETCH_FAILURE]: (state, payload) => {
        return Object.assign({}, state, {
            isFetching: false,
            statusText: `Error: ${payload.status} - ${payload.statusText}`
        });
    },
    [TERMINAL_MAKER_EDIT_REQUEST]: (state, payload) => {
        return Object.assign({}, state, {
            isFetching: true
        });
    },
    [TERMINAL_MAKER_EDIT_SUCCESS]: (state, payload) => {
        return Object.assign({}, state, {
            isFetching: false,
            statusText: 'Terminal Maker updated successfully.',
            data: payload.data
        });
    },
    [TERMINAL_MAKER_EDIT_FAILURE]: (state, payload) => {
        return Object.assign({}, state, {
            isFetching: false,
            statusText: `Error: ${payload.status} - ${payload.statusText}`
        });
    },
    [TERMINAL_MAKER_DELETE_REQUEST]: (state, payload) => {
        return Object.assign({}, state, {
            isFetching: true
        });
    },
    [TERMINAL_MAKER_DELETE_SUCCESS]: (state, payload) => {
        return Object.assign({}, state, {
            isFetching: false,
            statusText: 'Terminal Maker deleted successfully.',
            data: payload.data
        });
    },
    [TERMINAL_MAKER_DELETE_FAILURE]: (state, payload) => {
        return Object.assign({}, state, {
            isFetching: false,
            statusText: `Error: ${payload.status} - ${payload.statusText}`
        });
    }

});


