import React, { Component } from 'react';
import t from 'tcomb-form';
import ReadTerminalMaker from './read';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import * as terminalMakerActionCreators from '../../../actions/setup/terminal_maker';

const Form = t.form.Form;

const TerminalMakerForm = t.struct({
    maker_code: t.String,
    maker_name: t.String,
    address: t.String,
    contact_person: t.String,
    country_of_origin: t.String,
    contact_number: t.String
});

const TerminalMakerFormOptions = {
    error: <i>Form not filled completely</i>,
    auto: 'placeholders',
    help: <i>Create Terminal Maker</i>,
};


class TerminalMakerView extends Component {

    constructor(props) {
        super(props);
        this.state = {
            formValues: {
               maker_code: '',
               maker_name: '',
               address: '',
               contact_person: '',
               country_of_origin: '',
               contact_number: ''
            }
        }
    }

    _handleSubmit = (e) => {
        e.preventDefault();
        const value = this.refs.form.getValue();
        if (value) {
            const payload = {
                maker_code: this.state.formValues.maker_code,
                maker_name: this.state.formValues.maker_name,
                address: this.state.formValues.address,
                contact_person: this.state.formValues.contact_person,
                contact_number: this.state.formValues.contact_number,
                country_of_origin: this.state.formValues.country_of_origin
            }
            this.props.actions.createAction(payload);
        }
    };

    onFormChange = (value) => {
        this.setState({ formValues: value });
    };

    render() {
        if (this.props.statusText) {
            const statusTextClassNames = {
                'alert': true,
                'alert-danger': this.props.statusText.indexOf('A server error') === 0,
                'alert-success': this.props.statusText.indexOf('A server error') !== 0
            };

            let statusText = (
                <div className="row">
                    <div className="col-sm-12">
                        <div className={statusTextClassNames}>
                            {this.props.statusText}
                        </div>
                    </div>
                </div>
            );
        }
        return (
            <div className="col-sm-6 col-sm-offset-3">
                <form onSubmit={this._handleSubmit}>
                <Form ref='form'
                    type={TerminalMakerForm}
                    options={TerminalMakerFormOptions}
                    value={this.state.formValues}
                    onChange={this.onFormChange}
                />
                <button disabled={this.props.isFetching}
                    type="submit"
                    className="btn btn-primary btn-block"
                >
                    Submit
                </button>
            </form> 
            <ReadTerminalMaker />
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        isFetching: state.terminalMaker.isFetching,
        statusText: state.terminalMaker.statusText
    };
};

const mapDispatchToProps = (dispatch) => {
    return {
        dispatch,
        actions: bindActionCreators(terminalMakerActionCreators, dispatch)
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(TerminalMakerView);


