import { createReducer } from '../../../utils';
import {
    SIM_TYPE_CREATE_REQUEST,SIM_TYPE_CREATE_SUCCESS, SIM_TYPE_CREATE_FAILURE,
    SIM_TYPE_FETCH_REQUEST, SIM_TYPE_FETCH_SUCCESS, SIM_TYPE_FETCH_FAILURE,
    SIM_TYPE_EDIT_REQUEST, SIM_TYPE_EDIT_SUCCESS, SIM_TYPE_EDIT_FAILURE,
    SIM_TYPE_DELETE_REQUEST, SIM_TYPE_DELETE_SUCCESS, SIM_TYPE_DELETE_FAILURE
} from '../../../constants/setup/sim_type';

const initialState = {
    isFetching: false,
    statusText: null,
    data: null
};

export default createReducer(initialState, {
    [SIM_TYPE_CREATE_REQUEST]: (state, payload) => {
        return Object.assign({}, state, {
            isFetching: true,
            statusText: null
        });
    },
    [SIM_TYPE_CREATE_SUCCESS]: (state, payload) => {
        return Object.assign({}, state, {
            isFetching: false,
            statusText: 'Sim Type created successfully.'
        });
    },
    [SIM_TYPE_CREATE_FAILURE]: (state, payload) => {
        return Object.assign({}, state, {
            isFetching: false,
            statusText: `Error: ${payload.status} - ${payload.statusText}`
        });
    },
    [SIM_TYPE_FETCH_REQUEST]: (state, payload) => {
        return Object.assign({}, state, {
            isFetching: true
        });
    },
    [SIM_TYPE_FETCH_SUCCESS]: (state, payload) => {
        return Object.assign({}, state, {
            isFetching: false,
            statusText: 'Terminal Maker Read successfully.',
            data: payload.data
        });
    },
    [SIM_TYPE_FETCH_FAILURE]: (state, payload) => {
        return Object.assign({}, state, {
            isFetching: false,
            statusText: `Error: ${payload.status} - ${payload.statusText}`
        });
    },
    [SIM_TYPE_EDIT_REQUEST]: (state, payload) => {
        return Object.assign({}, state, {
            isFetching: true
        });
    },
    [SIM_TYPE_EDIT_SUCCESS]: (state, payload) => {
        return Object.assign({}, state, {
            isFetching: false,
            statusText: 'Sim Type updated successfully.',
            data: payload.data
        });
    },
    [SIM_TYPE_EDIT_FAILURE]: (state, payload) => {
        return Object.assign({}, state, {
            isFetching: false,
            statusText: `Error: ${payload.status} - ${payload.statusText}`
        });
    },
    [SIM_TYPE_DELETE_REQUEST]: (state, payload) => {
        return Object.assign({}, state, {
            isFetching: true
        });
    },
    [SIM_TYPE_DELETE_SUCCESS]: (state, payload) => {
        return Object.assign({}, state, {
            isFetching: false,
            statusText: 'Sim Type deleted successfully.',
            data: payload.data
        });
    },
    [SIM_TYPE_DELETE_FAILURE]: (state, payload) => {
        return Object.assign({}, state, {
            isFetching: false,
            statusText: `Error: ${payload.status} - ${payload.statusText}`
        });
    }

});


